import React, { Component } from 'react';
import './App.css';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank';
import Particles from 'react-particles-js';
import FaceRecognition from './components/FaceRecognition/FaceRecognition';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';

const particlesOptions = {
  "particles": {
    "number": {
      "value": 80,
      "density": {
        "enable": true,
        "value_area": 800
      }
    },
    "color": {
      "value": "#ffffff"
    },
    "shape": {
      "type": "polygon",
      "stroke": {
        "width": 0,
        "color": "#000000"
      },
      "polygon": {
        "nb_sides": 5
      },
      "image": {
        "src": "img/github.svg",
        "width": 100,
        "height": 100
      }
    },
    "opacity": {
      "value": 0.5,
      "random": false,
      "anim": {
        "enable": false,
        "speed": 1,
        "opacity_min": 0.1,
        "sync": false
      }
    },
    "size": {
      "value": 4.008530152163807,
      "random": true,
      "anim": {
        "enable": false,
        "speed": 40,
        "size_min": 0.1,
        "sync": false
      }
    },
    "line_linked": {
      "enable": true,
      "distance": 150,
      "color": "#ffffff",
      "opacity": 0.4,
      "width": 1
    },
    "move": {
      "enable": true,
      "speed": 6,
      "direction": "none",
      "random": false,
      "straight": false,
      "out_mode": "out",
      "bounce": false,
      "attract": {
        "enable": false,
        "rotateX": 600,
        "rotateY": 1200
      }
    }
  },
  "interactivity": {
    "detect_on": "canvas",
    "events": {
      "onhover": {
        "enable": true,
        "mode": "repulse"
      },
      "onclick": {
        "enable": true,
        "mode": "push"
      },
      "resize": true
    },
    "modes": {
      "grab": {
        "distance": 400,
        "line_linked": {
          "opacity": 1
        }
      },
      "bubble": {
        "distance": 400,
        "size": 40,
        "duration": 2,
        "opacity": 8,
        "speed": 3
      },
      "repulse": {
        "distance": 200,
        "duration": 0.4
      },
      "push": {
        "particles_nb": 4
      },
      "remove": {
        "particles_nb": 2
      }
    }
  },
  "retina_detect": true
}

const initialState = {
  input: '',
  imageUrl: '',
  box: {},
  celebrityMain: '',
  celebrityOther: '',
  route: 'signin',
  isSignedIn: false,
  user: {
    id: '',
    name: '',
    email: '',
    entries: 0,
    joined: ''
  }
}

class App extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  // load user function for signin and register modules
  loadUser = (data) => {
    this.setState({
        user: {
        id: data.id,
        name: data.name,
        email: data.email,
        entries: data.entries,
        joined: data.joined
        }
    })
  }

  calculateFaceLocation = (data) => {
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputImage');
    const width = Number(image.width);
    const height = Number(image.height);
    // return object to fill object in this.state
    return {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height),
    }
  }

  displayName = (celebrityArray) => {
    const capitalize = (fullName) => {
      // split the full name into an array
      return fullName.split(' ')
      // for each of the names, capitalise the first letter
      .map(name => {
        name = 
        name.substr(0,1).toUpperCase() + 
        name.substr(1);
        return name;
      })
      // join all the names back into a single string
      .join(' ')
    }
    // Output the most probable match
    const celebrityMain = capitalize(celebrityArray.shift().name)
    // Output all the other matches
    const celebrityOther = celebrityArray.map(celebrity => {return capitalize(celebrity.name)}).join(' / ');
    this.setState({celebrityMain: celebrityMain});
    this.setState({celebrityOther: celebrityOther});
  }

  displayFaceBox = (box) => {
    this.setState({box: box});
  }

  // create event handlers with custom names
  onInputChange = (event) => {
    this.setState({input: event.target.value});
  }

  onImageSubmit = () => {
      this.setState({imageUrl: this.state.input})
      fetch('http://localhost:3001/imageurl', {
          method: 'post',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
              input: this.state.input
          })
        }
      )
      // we cannot use imageURL here because of how setState works
      // common bug thats hard to detect 
      // setState is an async function that doesn't update until the render
      // caculate the face location first then push those numbers to the displayFaceBox function
      .then(response => response.json())
      .then(response => {
        if (response) {
            fetch('http://localhost:3001/image', {
            method: 'put',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                id: this.state.user.id
            })
          })
         .then(response => response.json())
         .then(count => {
            this.setState(Object.assign(this.state.user, { entries: count }));
            // this would replace the entire object
            // {user: {
            //    entries: count
            // }}
            this.displayFaceBox(this.calculateFaceLocation(response));
            this.displayName(response.outputs[0].data.regions[0].data.face.identity.concepts);
          })
          .catch(err => { console.log(err) })
        }
      }).catch(err => console.log(err))  
  }

  onRouteChange = (route) => {
    if (route === 'signout') {
      this.setState(initialState);
    } else if (route === 'home') {
      this.setState({isSignedIn: true});
    } else if (route === 'signin') {
      this.setState(initialState);
    }
    this.setState({route: route});
  }

  render() {
    return (
      <div className="App">
        <Particles className="particles"
              params={particlesOptions}
            />
        <Navigation onRouteChange={this.onRouteChange} isSignedIn={this.state.isSignedIn} route={this.state.route}/>
        { this.state.route === 'home'
        ? <div> 
            <Logo />
            <Rank name={this.state.user.name} entries={this.state.user.entries}/>
            <ImageLinkForm onInputChange={this.onInputChange} onImageSubmit={this.onImageSubmit}/>
            <FaceRecognition imageUrl={this.state.imageUrl} celebrityMain={this.state.celebrityMain} celebrityOther={this.state.celebrityOther} box={this.state.box}/>
          </div>
        : ( this.state.route === 'signin' || this.state.route === 'signout'
        ? <SignIn loadUser={this.loadUser} onRouteChange={this.onRouteChange}/>
        :
        <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange}/>
          )
        }
      </div>
    );
  }
}

export default App;



